:root {
  --color-white: #fff;
  --color-black: #000;
  --color-tamarillo: #8b1a12;
  --color-apricot: #ec7973;
  --color-shilo: #eab5b7;
  --color-pink: #fdedee;
  --color-bunker: #161920;
  --color-tundora: #424242;
  --color-pewter: #9d9f9e;
  --color-black-haze: #eff0f0;
  --color-mercury: #e5e5e5;
}

/* .light-theme {
	--color-bg: #fff;
}

.dark-theme {
	--color-bg: #191919;
} */
.btn-reset, .link-reset, .input-reset, .list-reset, .text-reset {
  margin: 0;
  padding: 0;
  border: 0;
}

*,
*::before,
*::after {
  -moz-box-sizing: inherit;
  -webkit-box-sizing: inherit;
  box-sizing: inherit;
}

.html {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  height: 100%;
  width: 100%;
  scroll-behavior: smooth;
  font-size: 100%;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

.body {
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 100%;
  background-color: var(--color-bg);
  font-family: "Montserrat", sans-serif;
  font-size: 16px;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

.header {
  flex: 0 0 auto;
}

.main-content {
  flex: 1 0 auto;
  overflow: hidden;
}

.footer {
  flex: 0 0 auto;
}

.lock {
  overflow: hidden;
}

.ios-lock {
  overflow: hidden;
  position: relative;
  height: 100vh;
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
}

.container {
  max-width: 1230px;
  padding-left: 15px;
  padding-right: 15px;
  margin: 0 auto;
}

@media (max-width: 991.98px) and (orientation: portrait) {
  .container {
    padding-left: 24px;
    padding-right: 24px;
  }
}

@media (max-width: 767.98px) and (orientation: portrait) {
  .container {
    padding-left: 24px;
    padding-right: 24px;
  }
}

@media (max-height: 479.98px) and (max-width: 1023px) and (orientation: landscape) {
  .container {
    padding-left: 50px;
    padding-right: 50px;
  }
}

/*! Basic */
.visually-hidden {
  position: absolute !important;
  clip: rect(1px 1px 1px 1px);
  /* IE6, IE7 */
  clip: rect(1px, 1px, 1px, 1px);
  height: 1px !important;
  width: 1px !important;
  padding: 0 !important;
  border: 0 !important;
  overflow: hidden;
}

/*! Reset classes */
.btn-reset {
  width: auto;
  margin: 0;
  padding: 0;
  background: transparent;
  outline: 0;
  color: inherit;
  font-family: inherit;
  font: inherit;
  cursor: pointer;
  -webkit-font-smoothing: inherit;
  -moz-osx-font-smoothing: inherit;
  -webkit-appearance: none;
}

.btn-reset::-moz-focus-inner {
  border: 0;
  padding: 0;
}

@media (min-width: 992px) {
  .btn-reset:focus {
    box-shadow: 0 0 0 3px #8edc44;
    border-radius: 8px;
  }
  .btn-reset:focus-visible {
    box-shadow: 0 0 0 3px #8edc44;
    border-radius: 8px;
  }
  .btn-reset:focus:not(:focus-visible) {
    box-shadow: none;
    border-radius: 0;
  }
}

.link-reset {
  outline: 0;
  color: inherit;
  text-decoration: none;
}

.link-reset:visited {
  text-decoration: none;
}

@media (min-width: 992px) {
  .link-reset:focus {
    box-shadow: 0 0 0 3px #8edc44;
    border-radius: 8px;
  }
  .link-reset:focus-visible {
    box-shadow: 0 0 0 3px #8edc44;
    border-radius: 8px;
  }
  .link-reset:focus:not(:focus-visible) {
    box-shadow: none;
    border-radius: 0;
  }
}

.input-reset {
  outline: 0;
  -webkit-appearance: none;
}

.input-reset::-ms-clear {
  display: none;
}

.input-reset::-webkit-search-cancel-button, .input-reset::-webkit-search-decoration {
  -webkit-appearance: none;
  appearance: none;
}

@media (min-width: 992px) {
  .input-reset:focus {
    box-shadow: 0 0 0 3px #8edc44;
    border-radius: 5px;
  }
  .input-reset:focus-visible {
    box-shadow: 0 0 0 3px #8edc44;
    border-radius: 5px;
  }
  .input-reset:focus:not(:focus-visible) {
    box-shadow: none;
    border-radius: 0;
  }
}

.list-reset {
  text-indent: 0;
  list-style: none;
  list-style-type: none;
  line-height: 1;
}

.text-reset {
  font-size: inherit;
  font-weight: 400;
  font-style: normal;
  line-height: 1;
}

.main-heading {
  color: var(--color-black);
  font-size: 46px;
  font-weight: 700;
  line-height: 130%;
}

@media (max-width: 767.98px) {
  .main-heading {
    font-size: 40px;
  }
}

.main-text {
  font-weight: 400;
  font-size: 16px;
  line-height: 140%;
}

.main-btn-link {
  display: inline-block;
  padding: 12px 41px;
  border-radius: 8px;
  border: 1px solid var(--color-tamarillo);
  font-weight: 500;
  font-size: 16px;
  line-height: 140%;
  transition: color 0.3s ease, background-color 0.3s ease;
}

.main-btn-link--red {
  background-color: var(--color-tamarillo);
  color: var(--color-white);
}

@media (min-width: 992px) {
  .main-btn-link--red:hover {
    background-color: var(--color-white);
    color: var(--color-tamarillo);
    transition: color 0.3s ease, background-color 0.3s ease;
  }
}

.main-btn-link--white {
  background-color: var(--color-white);
  color: var(--color-tamarillo);
}

@media (min-width: 992px) {
  .main-btn-link--white:hover {
    background-color: var(--color-tamarillo);
    color: var(--color-white);
    transition: color 0.3s ease, background-color 0.3s ease;
  }
}

.burger {
  position: relative;
  width: 30px;
  height: 18px;
  z-index: 2;
}

.burger__line {
  position: absolute;
  left: 0;
  top: 50%;
  width: 100%;
  height: 2px;
  background-color: var(--color-white);
  transform: translateY(-50%);
  transition: transform 0.2s ease 0s;
}

.burger::before, .burger::after {
  content: "";
  position: absolute;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: var(--color-white);
  transition: background-color 0.2s ease, transform 0.2s ease, top 0.2s ease, bottom 0.2s ease;
}

.burger::before {
  top: 0;
}

.burger::after {
  bottom: 0;
}

.burger--active span {
  background-color: var(--color-black);
  transform: translateY(-50%) scale(0);
  transition: background-color 0.2s ease, transform 0.2s ease, top 0.2s ease, bottom 0.2s ease;
}

.burger--active::before {
  background-color: var(--color-black);
  top: 8px;
  transform: rotate(45deg);
  transition: background-color 0.2s ease, transform 0.2s ease, top 0.2s ease, bottom 0.2s ease;
}

.burger--active::after {
  background-color: var(--color-black);
  bottom: 8px;
  transform: rotate(-45deg);
  transition: background-color 0.2s ease, transform 0.2s ease, top 0.2s ease, bottom 0.2s ease;
}

.burger--scroll .burger__line, .burger--scroll::before, .burger--scroll::after {
  background-color: var(--color-black);
}

.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  padding: 16px 0;
}

@media (max-width: 991.98px) {
  .header {
    padding: 10px 0;
  }
}

.header--active {
  padding: 9px 0;
  background-color: var(--color-white);
}

.header--active .header__image {
  width: 62px;
  height: 62px;
}

@media (max-width: 991.98px) {
  .header--active .header__image {
    width: 38px;
    height: 38px;
  }
}

.header--active .header__link {
  color: var(--color-black);
}

.header__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.header__logo {
  position: relative;
  z-index: 1;
}

.header__image {
  width: 80px;
  height: 80px;
  object-fit: cover;
}

@media (max-width: 991.98px) {
  .header__image {
    width: 60px;
    height: 60px;
  }
}

@media (max-width: 991.98px) {
  .header__nav {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    overflow: hidden;
    transform: translateX(-110%);
    transition: transform 0.3s ease;
  }
  .header__nav--active {
    transform: translateX(0);
    transition: transform 0.3s ease;
  }
}

@media (max-width: 991.98px) {
  .header__nav-body {
    display: flex;
    align-items: center;
    flex-direction: column;
    height: 100%;
    padding-top: 100px;
    padding-bottom: 130px;
    overflow: auto;
    background: var(--color-white) url(../img/menu-bg.jpg) center/cover no-repeat;
  }
}

.header__list {
  display: flex;
}

@media (max-width: 991.98px) {
  .header__list {
    align-items: center;
    justify-content: center;
    flex-direction: column;
  }
}

@media (min-width: 992px) {
  .header__item:not(:last-child) {
    margin-right: 53px;
  }
}

@media (max-width: 991.98px) {
  .header__item:not(:last-child) {
    margin-bottom: 40px;
  }
}

.header__link-diet--hidden {
  display: none;
  margin-top: 70px;
}

@media (max-width: 991.98px) {
  .header__link-diet--hidden {
    display: block;
  }
}

.header__link {
  position: relative;
  display: inline-block;
  color: var(--color-white);
  font-weight: 500;
  font-size: 16px;
  line-height: 140%;
}

.header__link::after {
  content: "";
  position: absolute;
  bottom: -2px;
  left: 0;
  width: 0;
  height: 2px;
  background-color: var(--color-tamarillo);
  will-change: width;
  transform: translateZ(0);
  transition: width 0.3s ease;
}

@media (min-width: 992px) {
  .header__link:hover::after {
    width: 100%;
    transition: width 0.3s ease;
  }
}

@media (max-width: 991.98px) {
  .header__link {
    color: var(--color-black);
    font-size: 24px;
    font-weight: 600;
  }
}

.header__burger {
  display: none;
}

@media (max-width: 991.98px) {
  .header__burger {
    display: block;
  }
}

main {
  padding-top: 200px;
}

.body {
  background-color: #191919;
}
